*, :before, :after {
  box-sizing: border-box;
  border-width: 0;
}

html {
  font-family: Arial;
  line-height: 1.5;
}

h1 {
  font-size: inherit;
  font-weight: inherit;
  margin: 0;
}

body {
  line-height: inherit;
  background-color: #121213;
  margin: 0;
}

button {
  font-family: inherit;
  font-size: 100%;
  font-weight: inherit;
  line-height: inherit;
  color: inherit;
  text-transform: none;
  margin: 0;
  padding: 0;
}

.main {
  height: calc(100vh - 82px);
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header {
  border-bottom: 1px solid #3a3a3c;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  padding-top: .5rem;
  padding-bottom: .5rem;
  display: flex;
}

.header > h1 {
  color: #fff;
  font-family: Trebuchet MS, Lucida Sans Unicode, Lucida Grande, Lucida Sans, Arial, sans-serif;
  font-size: 2.25rem;
  font-weight: 800;
  line-height: 2.5rem;
}

.header > * {
  color: #fff;
  fill: #fff;
}

.header > a {
  align-items: center;
  gap: .5rem;
  display: flex;
}

.header > a:hover, a:focus {
  opacity: .8;
  transition: all .2s ease-in-out;
}

.tile-grid {
  width: 350px;
  height: 420px;
  grid-template-rows: repeat(6, minmax(0, 1fr));
  gap: .25rem;
  margin: auto;
  display: grid;
}

.tile-row {
  grid-gap: .25rem;
  grid-template-columns: repeat(5, minmax(0, 1fr));
  display: grid;
}

.tile-row > div {
  color: #fff;
  border: 2px solid #3a3a3c;
  justify-content: center;
  align-items: center;
  font-size: 2.25rem;
  font-weight: 600;
  line-height: 2.5rem;
  display: flex;
}

@keyframes pop-tile {
  0% {
    transform: scale(1);
  }

  60% {
    transform: scale(1.1);
  }

  100% {
    ransform: scale(1);
  }
}

.tile-row > div.letter-added {
  border-color: #767676;
  animation: pop-tile 75ms ease-in-out;
}

.keyboard {
  max-width: 32rem;
  width: calc(100% - 16px);
}

.keyboard-row {
  color: #fff;
  width: 100%;
  gap: .5rem;
  margin-bottom: .5rem;
  display: flex;
}

.keyboard-row > button {
  height: 58px;
  background-color: #818384;
  border-radius: 4px;
  flex: 1;
  font-size: 14px;
  font-weight: bold;
}

.keyboard-row > button:hover, .keyboard-row > button:active {
  opacity: .85;
  cursor: pointer;
  transition: all .1s ease-in-out;
}

.keyboard-row #ENTER, #DELETE {
  flex: 1.5;
}

.keyboard-row #DELETE {
  justify-content: center;
  align-items: center;
  display: flex;
}

.keyboard-row #DELETE > svg {
  fill: #fff;
  height: 24px;
}

.keyboard-row #ENTER {
  font-size: 12px;
}

.toast {
  width: max-content;
  background-color: #e2e2e2;
  border-radius: 8px;
  margin: auto;
  padding: 12px 16px;
  transition: opacity 1s ease-out;
  position: absolute;
  top: 100px;
  left: 0;
  right: 0;
}

.toast > p {
  margin: 0;
  font-weight: bold;
}

